@import url(http://fonts.cdnfonts.com/css/whitney-2);
html {
    height: 100%;
    font-size: 10px;
  }
  body {
    min-height: 100%;
  }
  *{
    padding:0;
    margin:0;
    box-sizing: border-box;
  }
html, body, #root, .App {
    height: 100%;
  }
.discord__homepage{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.discord__homepage-svg{width:40%}.discord__homepage-btn{width:15%}

.main{display:-webkit-flex;display:flex;height:100vh;font-size:1.5rem;font-family:'Whitney Semibold', sans-serif}.main__sidebar{-webkit-flex:0 0 18%;flex:0 0 18%;background-color:#2C2F33;color:#fff;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.main__sidebar-header{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;padding:1rem 1.5rem;border-bottom:1px solid #23272a}.main__sidebar-currentchannel{margin-right:auto}.main__sidebar-expandicon{cursor:pointer}.main__channels{color:gray}.main__channels-header{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;padding:1.2rem 1.2rem .9rem 1.2rem}.main__channels-expandicon{margin-right:1rem}.main__channels-addicon{margin-left:auto;cursor:pointer}.main__voice{margin-top:auto;border-bottom:1px solid #35383d}.main__details{-webkit-flex:0 0 80%;flex:0 0 80%;background-color:#35383d}

.sidebarchannel{padding:0 2rem;font-size:1.75rem;cursor:pointer}.sidebarchannel__hash{display:inline-block;padding:0 1rem;font-size:2.1rem}.sidebarchannel__content{margin-bottom:.2rem;padding-bottom:.2rem}.sidebarchannel__content:hover{background-color:#40454b;border-radius:.5rem;color:#e6e8e8}

.profile{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;padding:1rem;background-color:#2a2b2f}.profile__avatar{margin-right:auto;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.profile__name{margin-left:1rem;color:#fbfcfc}

.sidebarvoice{background-color:#2a2b2f;padding:.2rem .1rem 1.5rem .1rem}.sidebarvoice__header{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;padding:0 1rem .5rem 1rem}.sidebarvoice__connect{color:#43b380;margin-right:auto;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.sidebarvoice__connect-connected{font-size:1.5rem;cursor:pointer}.sidebarvoice__connect-connected:hover{text-decoration:underline}.sidebarvoice__disconnect{color:#babbbf}.sidebarvoice__stream{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;padding:0 20px}.sidebarvoice__stream-video{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.sidebarvoice__stream-screen{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}

